* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

body {
	background-color: #292524;
	color: #fafaf9;
	font-family: 'Sono', sans-serif;
	padding: 48px 64px 0px;
}

/* MAIN LAYOUT */
/* tiene todo el contenido de la pagina, para que tenga sentido ser centrado, es 1500px, margin 0 auto. 0 en topleft, auto en laterales
Ademas en React es root ID no la clase container asi que debemos cambiarlo de
.containere a #root
*/

#root {
	max-width: 1500px;
	margin: 0 auto;
}

section {
	overflow: scroll;
	padding-bottom: 40px;
}
/* flex los ordena horizontla, elign item es centrado vertical, justificar con espaico entre items equidistantes, notar que el logo es un div para los 1 primeros elementros */

.disputed {
	color: #ef4444;
	font-weight: 600;

	margin-right: 10px;
}

.header {
	margin-bottom: 40px;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

/* grid sobre flex, primera columna de rango fijo es mejor grid, 1fr es para ocupr todo el restante, grid tiene propiedad gap tb */
.main {
	display: grid;
	grid-template-columns: 250px 1fr;
	gap: 48px;
	height: calc(100vh - 48px - 68px - 40px);
	overflow: hidden;
}

@media (max-width: 900px) {
	.main {
		grid-template-columns: 1fr;
	}
}

/* ELEMENTS */

.logo {
	display: flex;
	align-items: center;
	gap: 16px;
}

.logo img {
	width: 68px;
	height: 68px;
}

h1 {
	font-size: 42px;
	text-transform: uppercase;
	font-family: 'Coiny', sans-serif;
	line-height: 1;
	margin-top: 6px;
}

.category {
	margin-bottom: 16px;
}

.fact {
	font-size: 20px;
	line-height: 1.4;
	background-color: #44403c;
	margin-bottom: 16px;
	padding: 16px 24px;
	letter-spacing: -1px;
	border-radius: 16px;

	display: flex;
	align-items: center;
	gap: 24px;
}

@media (max-width: 1200px) {
	.fact {
		flex-direction: column;
		align-items: flex-end;
		gap: 12px;
	}
}

.tag {
	text-transform: uppercase;
	font-size: 14px;
	font-family: 'Coiny', sans-serif;
	padding-top: 3px;
	padding-left: 10px;
	padding-right: 10px;
	border-radius: 100px;
}

.vote-buttons {
	margin-left: auto;
	flex-shrink: 0;

	display: flex;
	gap: 8px;
}

.vote-buttons button {
	border: none;
	background-color: #78716c;
	font-size: 18px;
	padding: 6px 12px;
	border-radius: 100px;
	color: inherit;
	font-family: inherit;
	font-weight: 600;
	cursor: pointer;
	transition: 0.3s;
}

.vote-buttons button:hover {
	background-color: #292524;
}

.vote-buttons button:disabled {
	background-color: #44403c;
}

ul {
	list-style: none;
}

.source:link,
.source:visited {
	color: #a8a29e;
	text-decoration: none;
	margin-left: 12px;
	transition: 0.3s;
}

.source:hover,
.source:active {
	color: #3b82f6;
}

.btn {
	border: none;
	font-family: 'Coiny', sans-serif;
	line-height: 1;
	text-transform: uppercase;
	font-size: 17px;
	padding: 16px 0 13px;
	background-image: linear-gradient(135deg, #3b82f6, #ef4444, #16a34a, #eab308);
	color: inherit;
	border-radius: 100px;
	cursor: pointer;
	transition: 0.3s;
}

.btn:hover {
	transform: scale(110%) rotate(-2deg);
}

.btn-large {
	font-size: 20px;
	padding: 20px 32px 17px;
}

.btn-all-categories {
	margin-bottom: 16px;
	width: 100%;
}

.btn-category {
	width: 100%;
	background-image: none;
}

/* FORM */
.fact-form {
	background-color: #44403c;
	margin-bottom: 40px;
	padding: 16px 32px;
	display: flex;
	align-items: center;
	gap: 16px;
	border-radius: 16px;
}

.fact-form input,
.fact-form select {
	width: 220px;
	background-color: #78716c;
	border: none;
	border-radius: 100px;
	padding: 16px;
	font-size: 18px;
	color: inherit;
	font-family: inherit;
}

.fact-form input::placeholder {
	color: #a8a29e;
}

.fact-form input:first-child {
	flex-grow: 1;
}

.fact-form span {
	font-weight: 600;
	font-size: 18px;
	margin-right: 18px;
}

@media (max-width: 1000px) {
	body {
		padding: 32px 48px;
	}

	.fact-form {
		flex-direction: column;
		align-items: stretch;
	}

	.fact-form input,
	.fact-form select {
		width: auto;
	}
}
.hidden {
	display: none;
}

.message {
	text-align: center;
	text-transform: uppercase;
	font-size: 32px;
	font-weight: 600;
	margin-top: 20px;
}

@media (max-width: 900px) {
	.main {
		grid-template-columns: 1fr;
		height: auto;
	}
}

@media (max-width: 600px) {
	#root {
		padding: 24px;
	}

	.header {
		flex-direction: column;
		gap: 16px;
	}

	.fact-form {
		padding: 16px;
	}

	.fact {
		padding: 12px 16px;
	}
}

/* width */
::-webkit-scrollbar {
	width: 10px;
}

/* Track */
::-webkit-scrollbar-track {
	box-shadow: inset 0 0 1px grey;
	border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
	background: #525252;
	border-radius: 10px;
}
